import 'ol/ol.css';
import 'ol-layerswitcher/dist/ol-layerswitcher.css';

import ImageWMS from 'ol/source/ImageWMS';
import Map from 'ol/Map';
import XYZ from 'ol/source/XYZ';
import View from 'ol/View';
import { Overlay } from 'ol';
import { fromLonLat, toLonLat } from 'ol/proj';
import TileLayer from 'ol/layer/Tile';
import ImageLayer from 'ol/layer/Image';
import { toStringHDMS } from 'ol/coordinate';

let map = new Map({
	target: 'map',
	layers: [
		new TileLayer({
			source: new XYZ({
				url:
					'https://maps.geoapify.com/v1/tile/dark-matter/{z}/{x}/{y}.png?apiKey=' +
					'f37224251b89444b9394b4d49f3551c6'
			})
		}),
		new ImageLayer({
			extent: [-9074603.998, 5483898.1573, -8885040.1679, 5629434.2591],
			source: new ImageWMS({
				url: 'http://10.90.30.74:8085/geoserver/wms',
				params: { LAYERS: 'postgres-db:nominal_trunkline0' },
				ratio: 1,
				serverType: 'geoserver'
			})
		}),
		new ImageLayer({
			extent: [-9074603.998, 5483898.1573, -8885040.1679, 5629434.2591],
			source: new ImageWMS({
				url: 'http://10.90.30.74:8085/geoserver/wms',
				params: { LAYERS: 'postgres-db:nominal_pedestal0' },
				ratio: 1,
				serverType: 'geoserver'
			})
		})
	],
	view: new View({
		center: fromLonLat([-80.94349, 44.56717]),
		zoom: 10
	})
});

let closer = document.getElementById('popup-closer');
/**
 * Create an overlay to anchor the popup to the map.
 */
/**
 * Add a click handler to hide the popup.
 * @return {boolean} Don't follow the href.
 */
closer.onclick = function () {
	Overlay.setPosition(0, 0, 0);
	closer.blur();
	return false;
};
/**
 * Our Map Feature Info Below
 *
 */
const pos = fromLonLat([-80.94349, 44.56717]);

// Popup showing the position the user clicked
const popup = new Overlay({
	element: document.getElementById('popup')
});
map.addOverlay(popup);

// Vienna marker
const marker = new Overlay({
	position: pos,
	// positioning: 'center-center',
	element: document.getElementById('marker'),
	stopEvent: false
});
map.addOverlay(marker);

// Vienna label
const vienna = new Overlay({
	position: pos,
	element: document.getElementById('vienna')
});
map.addOverlay(vienna);

const element = popup.getElement();
map.on('click', function (evt) {
	const coordinate = evt.coordinate;
	const hdms = toStringHDMS(toLonLat(coordinate));
	popup.setPosition(coordinate);
	let popover = bootstrap.Popover.getInstance(element);
	if (popover) {
		popover.dispose();
	}
	popover = new bootstrap.Popover(element, {
		animation: false,
		container: element,
		content: '<p>The location you clicked was:</p><code>' + hdms + '</code>',
		html: true,
		placement: 'top',
		title: 'GIS@GBTel'
	});
	popover.show();
});
